h1:not([class]), .h1{
  font-size: 48px;
  font-weight: 700;
  line-height: 50px;

  @media only screen and (max-width: $bp-xxs) {
    font-size: 32px;
    line-height: 34px;
  }
}

h2:not([class]), .h2{
  font-size: 32px;
  font-weight: 700;
  line-height: 50px;

  @media only screen and (max-width: $bp-xxs) {
    font-size: 22px;
    line-height: 34px;
  }
}


h3:not([class]), .h3{
  font-size: 22px;
  line-height: 34px;

  @media only screen and (max-width: $bp-xxs) {
    font-size: 18px;
    line-height: 28px;
  }
}

.text {

  &-color--white {
    color: $white;
  }

  &-color--turquoise {
    color: $turquoise;
  }

  &-regular {
    p {
      font-size: 16px;
      line-height: 25px;
    }
  }

  &-lg {
    p {
      font-size: 20px;
      line-height: 31px;

      @media only screen and (max-width: $bp-sm) {
        font-size: 18px;
        line-height: 28px;
      }
    }
  }

  &-xl {
    p {
      font-size: 22px;
      line-height: 34px;

      @media only screen and (max-width: $bp-xxs) {
        font-size: 18px;
        line-height: 28px;
      }
    }
  }
}


p {
  margin-top: 0;
  margin-bottom: 26px;

  &:last-of-type {
    margin-bottom: 0;
  }
}


.list {
  margin: 0;
  padding: 0;

  &__item {
    position: relative;
    padding-left: 24px;
    margin-bottom: 28px;

    &:last-of-type {
      margin-bottom: 0;
    }

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 8px;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: $turquoise;
    }
  }

  &--checked {
    li {
      position: relative;
      padding-left: 28px;

      &:before {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 16px;
        height: 16px;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 16 16' fill='none'%3E%3Cpath d='M6.23428 11.6659C6.30977 11.7693 6.40928 11.8535 6.52457 11.9116C6.63986 11.9697 6.76761 12 6.89722 12C7.02684 12 7.15459 11.9697 7.26988 11.9116C7.38517 11.8535 7.48468 11.7693 7.56017 11.6659L12.9612 4.31952C13.0587 4.18618 12.9612 4 12.7945 4H11.5917C11.3301 4 11.0814 4.12328 10.9275 4.33461L6.89851 9.81925L5.07252 7.33355C4.91864 7.12473 4.67244 6.99894 4.40829 6.99894H3.20549C3.03879 6.99894 2.94134 7.18511 3.03879 7.31846L6.23428 11.6659V11.6659Z' fill='%2300A0A3'%3E%3C/path%3E%3C/svg%3E ");
      }
    }
  }

  &--checked--white {
    li {
      position: relative;
      padding-left: 28px;
      margin-bottom: 6px;

      &:last-of-type {
        margin-bottom: 0;
      }

      &:before {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 16px;
        height: 16px;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 16 16' fill='none'%3E%3Cpath d='M6.23428 11.6659C6.30977 11.7693 6.40928 11.8535 6.52457 11.9116C6.63986 11.9697 6.76761 12 6.89722 12C7.02684 12 7.15459 11.9697 7.26988 11.9116C7.38517 11.8535 7.48468 11.7693 7.56017 11.6659L12.9612 4.31952C13.0587 4.18618 12.9612 4 12.7945 4H11.5917C11.3301 4 11.0814 4.12328 10.9275 4.33461L6.89851 9.81925L5.07252 7.33355C4.91864 7.12473 4.67244 6.99894 4.40829 6.99894H3.20549C3.03879 6.99894 2.94134 7.18511 3.03879 7.31846L6.23428 11.6659Z' fill='white'%3E%3C/path%3E%3C/svg%3E ");
      }
    }
  }
}

.blockquote {
  padding-left: 30px;
  border-left: 2px solid $turquoise;
  margin: 30px 0;

  p {
    font-size: 24px;
    line-height: 1.55;
    font-weight: 400;

  }
}

a:not([class]),
.link {
  text-decoration: none;
  color: $turquoise;
}
