.main {
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 100vh;

  @media only screen and (max-width: $bp-lg) {
    padding-top: 80px;
  }
}

.container {
  width: 100%;
  max-width: 1150px;
  margin-left: auto;
  margin-right: auto;

  @media only screen and (max-width: $bp-lg) {
    max-width: 100%;
    padding-left: 40px;
    padding-right: 40px;
  }

  @media only screen and (max-width: $bp-md) {
    padding-left: 60px;
    padding-right: 60px;
  }

  @media only screen and (max-width: $bp-xxs) {
    padding-left: 32px;
    padding-right: 32px;
  }

  &--sm {
    max-width: 960px;
  }
}


.section-img {
  height: 700px;
  object-fit: cover;
  object-position: center top;

  @media only screen and (max-width: $bp-md) {
    height: 336px;
  }

  @media only screen and (max-width: $bp-sm) {
    height: 258px;
  }

  @media only screen and (max-width: $bp-xxs) {
    height: 210px;
  }

  @media only screen and (max-width: 450px) {
    height: 168px;
  }

}

.scroll-button {
  position: fixed;
  z-index: 10;
  right: 30px;
  bottom: 30px;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: opacity $default-transition;

  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
}

.content-block {
  ul {
    margin-left: 20px;
    list-style: disc;
    margin-bottom: 26px;
  }

  h1 {
    font-size: 36px;
    line-height: 1.23;
    margin-bottom: 60px;

    @media only screen and (max-width: $bp-lg) {
      font-size: 32px;
      line-height: 38px;
    }

    @media only screen and (max-width: $bp-xs) {
      font-size: 28px;
      line-height: 34px;
    }
  }
}
