@-webkit-keyframes tabAnim {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes tabAnim {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


html.is-fixed {
  height: 100vh;
  overflow: hidden;
}

.img-responsive {
  width: 100%;
  max-width: 100%;
}

.pt-0 {
  padding-top: 0 !important;
}

.d-none {
  display: none !important;
}

.d-lg-none {
  @media only screen and (max-width: $bp-lg) {
    display: none !important;
  }
}

.mt-0 {
  margin-top: 0!important;
}

[data-aos=fade-up] {
  @media only screen and (max-width: $bp-md) {
    opacity: 1 !important;
    transform: none !important;
  }
}

.overflow-hidden {
  overflow: hidden;
}